.movieList {
  display: grid;
  grid-template-columns: auto auto auto;
}

.movie {
  height: 350px;
  width: 220px;
  margin: 10px auto;
  text-align: center;
  border-radius: 8px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  position: relative;
  transition: transform 0.5s;
  border: 2px solid white;
  background-size: "cover";
  background-position: "center";
}
.movie:hover {
  transform: scale(1.05);
}
.titleStyle {
  font-size: 20px;
}

/* .movie img {
  width: 140px;
  height: 200px;
  margin-bottom: 15px;
  margin-top: 8px;
  border-radius: 5px;
} */
.movie div {
  margin-bottom: 10px;
  color: white;
  font-weight: bold;
  margin-top: 20px;
  text-align: left;
  margin-left: 10px;
}
.movieListContent {
  width: 70%;
}
.contentDisplay {
  display: flex;
  flex-direction: row;
}

/* the text style under the search bar */
.listText {
  font-size: 25px;
  margin-bottom: 15px;
  margin-top: 10px;
  font-weight: bold;
  color: white;
  text-shadow: 2px 2px 4px #000000;
}

@media screen and (max-width: 992px) {
  .movieList {
    display: grid;
    grid-template-columns: auto auto;
  }
}

@media screen and (max-width: 700px) {
  .movieList {
    display: flex;
    flex-direction: column;
  }
  .movieListContent {
    width: 50%;
  }
  .movie {
    width: 250px;
    height: 180px;
  }
}
